@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 16px;
  }
}

.main-heading {
  margin: 0;
  padding: 0;
  margin-bottom: 50px;
  font-size: 32px;
  font-weight: 600;
  line-height: normal;
  text-align: center;
  color: #1c63b8;
}
.main-heading--size {
  text-align: start;
  width: 430px;
  font-size: 36px;
  margin-bottom: 25px;
}
@media (max-width: 1439px) {
  .main-heading--size {
    width: auto;
  }
}

.page-heading {
  margin: 0;
  padding: 0;
  margin-bottom: 9px;
  font-size: 24px;
  font-weight: 600;
  line-height: normal;
  color: #1c63b8;
}
.page-heading--contacts {
  margin-bottom: 48px;
}

.page-text {
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  color: #828282;
}

.header {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.1);
}
.header__wrapper {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding-top: 19px;
  padding-bottom: 19px;
}
.header__wrapper--opened {
  position: fixed;
  z-index: 12;
  top: 0;
  left: 0;
  padding-bottom: 0;
  height: 100vh;
  width: 100vw;
  display: block;
}
.header__logo-wrapper {
  display: flex;
  align-items: center;
}
.header__logo-wrapper--opened {
  padding-bottom: 19px;
  padding-left: 16px;
  padding-right: 16px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.1);
}
@media (max-width: 767px) {
  .header__logo-wrapper {
    width: 100%;
  }
}
.header__logo-image--mobile {
  display: none;
}
@media (max-width: 1023px) {
  .header__logo-image--mobile {
    display: block;
  }
}
@media (max-width: 1023px) {
  .header__logo-image--desctop {
    display: none;
  }
}
.header__toggle {
  width: 35px;
  height: 26px;
  padding: 0;
  margin-left: auto;
  background-color: transparent;
  background-repeat: no-repeat;
  outline: none;
  border: none;
  display: none;
}
.header__toggle--closed {
  background-image: url("../img/svg/icon-menu-burger.svg");
}
.header__toggle--opened {
  width: 25px;
  background-image: url("../img/svg/icon-menu-cross.svg");
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
  }
}

.main-nav {
  margin-left: auto;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
@media (max-width: 767px) {
  .main-nav__list {
    display: none;
  }
  .main-nav__list--opened {
    display: block;
    margin-top: 25px;
  }
}
.main-nav__item {
  padding-right: 22px;
  margin-right: 22px;
  border-right: 1px solid #E0E0E0;
}
.main-nav__item:last-child {
  padding-right: 0;
  margin-right: 0;
  border-right: none;
}
@media (max-width: 1023px) {
  .main-nav__item {
    padding-right: 12px;
    margin-right: 12px;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    border: none;
    padding-left: 16px;
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.1);
    padding-top: 16px;
    padding-bottom: 16px;
    margin-right: 0;
    padding-right: 0;
  }
}
.main-nav__link {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: #333333;
}
@media (max-width: 767px) {
  .main-nav__link {
    font-size: 20px;
    display: block;
  }
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__link:hover, .main-nav__link:focus {
  opacity: 0.6;
}

