  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-position: 0 0;
  transform: translateY(-50%);
}
@media (max-width: 1023px) {
  .contacts__link {
    font-size: 16px;
    padding-left: 49px;
    display: block;
  }
}
.contacts__link--location::after {
  width: 46px;
  height: 46px;
  margin-left: -5px;
  background-image: url(../img/svg/location.svg);
}
.contacts__link--time::after {
  background-image: url(../img/svg/time.svg);
}
.contacts__link--phone::after {
  background-image: url(../img/svg/phone.svg);
}
.contacts__link--whatsapp::after {
  background-image: url(../img/svg/whatsapp.svg);
}
.contacts__link--telegram::after {
  background-image: url(../img/svg/telegram.svg);
}
.contacts__link--mail::after {
  background-image: url(../img/svg/mail.svg);
}

